import { redirect, json } from "@remix-run/node";
import { useEffect } from "react";
// import { commitSession, getSession } from "~/sessions.server";
// import { loginToExternalApi } from "~/auth.server";
import { Form, useActionData, useTransition } from "@remix-run/react";
import { commitSession,  } from "../../sessions.server";
import { loginToExternalApi } from "../../auth.server";

export async function action({ request }) {
    try {
        const formData = await request.formData();
        const email = formData.get("email");
        const password = formData.get("password");

        const response = await fetch("http://192.168.1.51:8056/auth/login", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ email, password }),
        });

        console.log("API status:", response.status);

        // Try reading raw response text
        const text = await response.text();
        console.log("Raw API response:", text);

        // Try to parse JSON safely
        let userData;
        try {
            userData = JSON.parse(text);
        } catch (err) {
            console.error("❌ Could not parse JSON:", err);
            return json({ error: "Invalid JSON response from API" }, { status: 500 });
        }

        console.log("Parsed user data:", userData);

        // store in session
        const session = await getSessio(request.headers.get("Cookie"));
        session.set("user", userData);

        return redirect("/dashboard", {
            headers: {
                "Set-Cookie": await commitSession(session),
            },
        });
    } catch (error) {
        console.error("🔥 Login failed:", error);
        return json({ error: "Internal Server Error" }, { status: 500 });
    }
}


export default function LoginPage() {
    const actionData = useActionData();
    const transition = useTransition();

    useEffect(() => {
        if (actionData?.error) {
            alert(actionData.error); // replace with toast if you prefer
        }
    }, [actionData]);

    return (
        <div className="flex justify-center items-center max-w-[600px]">
            <div className="w-full p-6 ">
                <h2 style={{ marginBottom: 12 }}>Login</h2>
                <Form method="post">
                    <input name="email" type="email" placeholder="Email" required style={{ width: "100%", padding: 8, marginBottom: 8 }} />
                    <input name="password" type="password" placeholder="Password" required style={{ width: "100%", padding: 8, marginBottom: 12 }} />
                    <button
                        type="submit"
                        disabled={transition.state === "submitting"}
                        style={{
                            width: "100%",
                            padding: 10,
                            background: "#2563eb",
                            color: "white",
                            border: "none",
                            borderRadius: 6,
                        }}
                    >
                        {transition.state === "submitting" ? "Logging in..." : "Login"}
                    </button>
                </Form>
            </div>
        </div>
    );
}
